import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import { Link } from "react-router-dom";
import Professional from "../../pdf/Resume.pdf";
import Coop from "../../pdf/Coop.pdf";

const useStyles = makeStyles(theme => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    width: 345,
    height: 300,
    boxShadow: theme.shadows[5],
    borderRadius: 10,
    outline: "none",
    padding: theme.spacing(0, 4, 3)
  }
}));

export default function TransitionsModal() {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <span>
      <a onClick={handleOpen} style={{ color: "red", cursor: "pointer" }}>
        &nbsp;resume&nbsp;
      </a>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500
        }}
      >
        <Fade
          in={open}
          style={{
            border: "none"
          }}
        >
          <div className={classes.paper}>
            <h2 id="transition-modal-title" style={{ textAlign: "center" }}>
              Choose CV
            </h2>
            <br />
            <p
              style={{ textAlign: "center" }}
              id="transition-modal-description"
            >
              <a href={Professional} target="_blank">
                <button
                  href="../../pdf/Resume.pdf"
                  type="button"
                  className="button button__accent"
                  onClick={handleClose}
                >
                  Professional
                </button>
              </a>
              <br />
              <a href={Coop} target="_blank">
                <button
                  type="button"
                  className="button button__accent"
                  onClick={handleClose}
                >
                  CO-OP
                </button>
              </a>
            </p>
          </div>
        </Fade>
      </Modal>
    </span>
  );
}
